<template>
  <div class="effect1">
    <div class="item_container">
      <div :class="'item item'+item" v-for="item in 4" :key="item">

      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: "effect1",
    data() {
      return {

      }
    }
  }
</script>
<style>
  .effect1 {
    width: 100vw;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;

  }

  .item_container {
    width: 100vw;
    ;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .item {
    width: 30px;
    height: 30px;
    border-radius: 50%;

    margin-left: 10px;
  }

  .item1 {
    animation: ditem1 4s linear infinite;
    background: rgb(47, 164, 245);
    transform-origin: -30px center;
  }

  .item2 {
    animation: ditem2 4s linear infinite;
    transform-origin: -30px center;
    background: rgb(248, 28, 49);
  }

  .item3 {
    animation: ditem3 4s linear infinite;
    transform-origin: -30px center;
    background: rgb(33, 248, 48);
  }

  .item4 {
    animation: ditem4 4s linear infinite;
    transform-origin: -30px center;

    background: rgb(245, 238, 47);
  }

  @keyframes ditem1 {
    2% {
      box-shadow: 0 0 20px rgba(47, 164, 245, 1);
    }

    /* 6% {
      box-shadow: 0 0 20px rgba(255, 119, 6, 1);
      transform: translateX(-15px) rotateZ(-90deg);
    } */
    10% {
      box-shadow: 0 0 20px rgba(47, 164, 245, 1);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    50% {
      box-shadow: 0 0 20px rgba(47, 164, 245, 0);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    65% {
      transform: translateX(-70px) rotateZ(-180deg);
    }

    67% {
      box-shadow: 0 0 20px rgba(47, 164, 245, 1);

    }

    75% {
      box-shadow: 0 0 20px rgba(47, 164, 245, 1);
      transform: rotateZ(0deg);
    }

    100% {
      transform: rotateZ(0deg);
    }
  }

  @keyframes ditem2 {
    5% {
      transform: rotateZ(0deg);
    }

    8% {
      box-shadow: 0 0 20px rgba(248, 28, 49, 1);
    }


    15% {
      box-shadow: 0 0 20px rgba(248, 28, 49, 1);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    50% {
      box-shadow: 0 0 20px rgba(248, 28, 49, 0);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    60% {
      transform: translateX(-70px) rotateZ(-180deg);
    }

    62% {
      box-shadow: 0 0 20px rgba(248, 28, 49, 1);

    }

    70% {
      box-shadow: 0 0 20px rgba(248, 28, 49, 1);
      transform: rotateZ(0deg);
    }

    100% {
      transform: rotateZ(0deg);
    }
  }

  @keyframes ditem3 {
    10% {
      transform: rotateZ(0deg);
    }

    12% {
      box-shadow: 0 0 20px rgba(33, 248, 48, 1);
    }

    /* 26% {
      box-shadow: 0 0 20px rgba(255, 119, 6, 1);
      transform: translateX(-15px) rotateZ(-90deg);
    } */
    20% {
      box-shadow: 0 0 20px rgba(33, 248, 48, 1);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    50% {
      box-shadow: 0 0 20px rgba(33, 248, 48, 0);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    55% {
      transform: translateX(-70px) rotateZ(-180deg);

    }

    57% {
      box-shadow: 0 0 20px rgba(33, 248, 48, 1);

    }

    65% {
      box-shadow: 0 0 20px rgba(33, 248, 48, 1);
      transform: rotateZ(0deg);
    }

    100% {
      transform: rotateZ(0deg);
    }
  }

  @keyframes ditem4 {
    15% {
      transform: rotateZ(0deg);
    }

    17% {
      box-shadow: 0 0 20px rgba(245, 238, 47, 1);

    }

    /* 36% {
      box-shadow: 0 0 20px rgba(255, 119, 6, 1);
      transform: translateX(-15px) rotateZ(-90deg);
    } */
    25% {
      box-shadow: 0 0 20px rgba(245, 238, 47, 1);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    50% {
      box-shadow: 0 0 20px rgba(245, 238, 47, 0);
      transform: translateX(-70px) rotateZ(-180deg);
    }

    52% {
      box-shadow: 0 0 20px rgba(245, 238, 47, 1);
    }

    60% {
      box-shadow: 0 0 20px rgba(245, 238, 47, 1);
      transform: rotateZ(0deg);
    }

    100% {
      transform: rotateZ(0deg);
    }
  }
</style>


