<template>
  <div class="hello">
    <div class="img_item_cotnainer">
      <div class="img_item img_item1">
        <!-- <img src="../assets/blue.png" alt=""> -->
      </div>
      <div class="img_item img_item2">
        <!-- <img src="../assets/red.png" alt=""> -->
      </div>
      <div class="img_item img_item3">
        <!-- <img src="../assets/yellow.png" alt=""> -->
      </div>
      <div class="img_item img_item4">
        <!-- <img src="../assets/green.png" alt=""> -->
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello {
    width: 100vw;
    height: 90vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .img_item_cotnainer {
    width: 100px;
    height: 100px;
    position: relative;
    transform-style: preserve-3D;
    animation: change 2s linear infinite;
  }

  @keyframes change {
    20% {
      transform: none;
    }

    60% {
      transform: rotateZ(90deg);
    }

    100% {
      transform: rotateZ(235deg);
    }
  }

  .img_item {
    position: absolute;
    transform-style: preserve-3D;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 100px;
    height: 100px;
  }

  .img_item1 {
    animation: effect1 2s linear infinite;
    z-index: 100;
    background: rgb(47, 164, 245);
  }

  .img_item2 {

    animation: effect2 2s linear infinite;
    z-index: 90;
    background: rgb(248, 28, 49);
  }

  .img_item3 {

    animation: effect3 2s linear infinite;
    z-index: 80;
    background: rgb(245, 238, 47);
  }

  .img_item4 {
    animation: effect4 2s linear infinite;
    z-index: 70;
    background: rgb(33, 248, 48);
  }

  .img_item_cotnainer img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }

  @keyframes effect1 {

    5% {
      transform: none;
    }

    20% {
      transform: translateX(100px) rotateY(-89deg);
      opacity: 0.8;
    }

    60% {
      transform: translateX(100px) rotateY(-180deg);
      opacity: 1;
    }

    100% {
      transform: translateX(0px) rotateY(-180deg);
      opacity: 0.8;
      box-shadow: 0 0 20px rgb(47, 164, 245);
    }
  }

  @keyframes effect2 {

    5% {
      transform: none;
    }

    20% {
      transform: translateX(-100px) rotateY(89deg);
      opacity: 0.8;
    }

    60% {
      transform: translateX(-100px) rotateY(180deg);
      opacity: 1;
    }

    100% {
      transform: translateX(0px) rotateY(180deg);
      opacity: 0.8;
      box-shadow: 0 0 20px rgb(248, 28, 49);
    }
  }

  @keyframes effect3 {

    5% {
      transform: none;
    }

    20% {
      transform: translateY(100px) rotateX(89deg);
      opacity: 0.8;
    }

    60% {
      transform: translateY(100px) rotateX(180deg);
      opacity: 1;
    }

    100% {
      transform: translateY(0px) rotateX(180deg);
      opacity: 0.8;
      box-shadow: 0 0 20px rgb(245, 238, 47);
    }
  }

  @keyframes effect4 {

    5% {
      transform: none;
    }

    20% {
      transform: translateY(-100px) rotateX(-89deg);
      opacity: 0.8;
    }

    60% {
      transform: translateY(-100px) rotateX(-180deg);
      opacity: 1;
    }

    100% {
      transform: translateY(0px) rotateX(-180deg);
      opacity: 0.8;
      box-shadow: 0 0 20px rgb(33, 248, 48);
    }
  }
</style>


